import styled from "styled-components";
import theme from "Document.theme.js";

export const SearchPlus = styled.div`
	display: flex;
	.Select-arrow-zone {
		cursor: pointer;
		display: table-cell;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 25px;
		padding-right: 5px;
	}
	.Select-clear-zone {
		-webkit-animation: Select-animation-fadeIn 200ms;
		animation: Select-animation-fadeIn 200ms;
		color: #999;
		cursor: pointer;
		/* display: table-cell; */
		display: none;
		position: relative;
		text-align: center;
		vertical-align: middle;
		width: 17px;
	}
`;



